@media (max-width: 767px) {
  .site-sections__link {
    font-size: 14px;
    line-height: 19px;
  }
}

.contacts__caption {
  margin-bottom: 23px;
}
@media (max-width: 767px) {
  .contacts__caption {
    margin-bottom: 13px;
  }
}

.contacts__info {
  color: rgba(248, 253, 255, 0.7);
  max-width: 325px;
  margin: 2px 0 9px;
}
@media (max-width: 1023px) {
  .contacts__info {
    max-width: 320px;
  }
}
@media (max-width: 767px) {
  .contacts__info {
    width: 100%;
    font-size: 14px;
    line-height: 19px;
  }
}

@media (max-width: 767px) {
  .contacts__info--address {
    margin-bottom: 22px;
  }
}

@media (max-width: 767px) {
  [data-contacts=is-opened] .contacts__toggle,
[data-contacts=is-closed] .contacts__toggle {
    display: block;
    border: none;
    background-color: rgba(255, 255, 255, 0);
    cursor: pointer;
    width: 15px;
    height: 15px;
    position: relative;
  }
}

[data-contacts=is-opened] .contacts__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ffffff;
  width: 15px;
  height: 1px;
}

[data-contacts=is-closed] .contacts__toggle::before, [data-contacts=is-closed] .contacts__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ffffff;
  width: 15px;
  height: 1px;
}
[data-contacts=is-closed] .contacts__toggle::after {
  transform: rotate(90deg);
}

@media (max-width: 767px) {
  [data-contacts=is-closed] .contacts__info {
    display: none;
  }
}

